.GameNight {
    background-color: #061A3B;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
}

.body {
    width: 100%
}

.body:after{
    display: table;
    clear: both;
}

.picker {
    text-align: right;
    width: 20%;
    float: left;
}

.board {
    width: 80%;
    float: left;
}

.GameNight p {
    text-align: left
}

ul#gamePicker {
    list-style: none;
}

ul#gamePicker li {
    margin: 10px;
    padding-right: 10px;
}

ul#gamePicker li:hover {
    cursor: pointer;
    color: #52bbae;
}

iframe#gameBoard {
    height: 85vh;
    width: 70vw;
}